* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.container {
  display: flex;
  flex-wrap: wrap;
}

.block {
  height: 300px;
  min-width: 300px;
  display: flex;
  flex: 0 1 33.33%;
  justify-content: center;
  align-items: center;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
  font-size: 100px;
  color: #fff;
  background-color: #f00;
}

.block--2 {
  background-color: #c00;
}

.block--3 {
  background-color: #900;
}

.block--4 {
  background-color: #600;
  order: 2;
}

.block--5 {
  background-color: #300;
  order: 1;
}

.block--6 {
  background-color: #000;
}

@media (max-width: 599px) {
  .block {
    flex-basis: 100%;
    order: initial;
  }
}

@media (min-width: 600px) and (max-width: 899px) {
  .block {
    flex-basis: 50%;
  }
  .block--3 {
    order: 3;
  }
  .block--5 {
    order: 4;
  }
  .block--6 {
    order: 5;
  }
}
